<div class="grid"><header><p>Gridswap from <a href="http://internet.game" target="_blank">internet.game</a></p><p>Click a square to turn it into an obstacle.</p></header><table><tr v-for="(row, y) in grid"><td v-for="(column, x) in row" :class="column.c" @click="toggleBlank(y, x, true)" :style="{backgroundColor: column.b}"><span class="moveCount" v-if="column.m &amp;&amp; column.m &gt;= 0">{{column.m}}</span><span class="direction" v-if="column.d" v-html="column.d"></span></td></tr></table><footer><button type="button" @click="resetGrid">reset</button><button type="button" @click="clearGrid">clear</button></footer></div>